import React from 'react';
import { NavLink } from "react-router-dom";


const Navbar = () =>{
    return(
         <nav class="navbar navbar-expand-lg">
            <div class="container">   
            <a class="navbar-brand" href="#">REAL<span>KEY</span></a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav ms-auto">
            <li class="nav-item">
            <NavLink exact activeClassName="active__class" className="nav-link" to="/Index">Home</NavLink>
            </li>
            <li class="nav-item">
            <NavLink exact activeClassName="active__class" className="nav-link" to="/About">About</NavLink>
            </li>
            <li class="nav-item">
            <NavLink exact activeClassName="active__class" className="nav-link" to="/Services">Services</NavLink>
            </li>
            <li class="nav-item">
            <NavLink exact activeClassName="active__class" className="nav-link" to="/Whyrealkey">Whyrealkey</NavLink>
            </li>
            <li class="nav-item">
            <NavLink exact activeClassName="active__class" className="nav-link" to="/Invest">Invest</NavLink>
            </li>
            <li class="nav-item">
            <NavLink exact activeClassName="active__class"  className="nav-link" to="/Contact">Contact</NavLink>
            </li>
           </ul>
           </div>
            </div>
            </nav>
       
    );
}


export default Navbar;