import React from 'react';
import Navbar from './Navbar';
import './Navbar.css';
import Footer from './Footer';
import './Contact.css';



const Contact = () =>{
    return(
        <React.Fragment>
           <div className="heloo">
              <Navbar/>
           </div>
            <div className="container py-5">
            <div className="row pt-5">
            <div className="col-lg-12 col-xl-12 col-sm-12">
                <h1 className="py-3">Contact section</h1>
                <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley</p>
            
            </div>

             <div className="col-lg-6 col-xl-6 col-sm-12">
             <form>
                <div class="mb-3">
                <label for="exampleInputEmail1" class="form-label">Email address</label>
                <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                <div id="emailHelp" class="form-text">We'll never share your email with anyone else.</div>
                </div>
                <div class="mb-3">
                <label for="exampleInputPassword1" class="form-label">Password</label>
                <input type="password" class="form-control" id="exampleInputPassword1"/>
                </div>
                <div class="mb-3 form-check">
                <input type="checkbox" class="form-check-input" id="exampleCheck1"/>
                <label class="form-check-label" for="exampleCheck1">Check me out</label>
                </div>
                <button type="submit" class="btn btn-primary">Submit</button>
            </form>
             </div>

             <div className="col-lg-6 col-xl-6 col-sm-12 px-5">
                 <h4 className="py-3">site map</h4>
                 <iframe className="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d30277.52672094004!2d73.84191073525429!3d18.452341041458318!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bc29538e5e1de63%3A0x3ec1484b987f3140!2sCafe%20Coffee%20Day!5e0!3m2!1sen!2sin!4v1620710646450!5m2!1sen!2sin"></iframe>
             </div>

            </div>
            </div>

            <Footer/>
        </React.Fragment>
    );
}


export default Contact;


