import React from 'react';
import Navbar from './Navbar';
import './Navbar.css';
import Footer from './Footer';

const cardImg1 = 'https://picsum.photos/id/1027/150/80';
const cardImg2 = 'https://picsum.photos/id/1047/150/80';
const cardImg3 = 'https://picsum.photos/id/1080/150/80';

const Services = () =>{
    return(
        <React.Fragment>
           <div className="heloo">
              <Navbar/>
           </div>
            
             
             <div className="container py-5">
                 <div className="row pt-5">
                 <div className="col-lg-12 col-xl-12 col-sm-12">
                 <h1 className="pb-3">Services section</h1>
                 </div>
                 <div className="col-lg-4 col-xl-4 col-sm-12 ">
                        <div class="card">
                        <div class="card-header text-center text-uppercase">
                           <h5>ajent finder</h5>
                        </div>
                        <img src={cardImg1} class="card-img-top" alt="..."/>
                        <div class="card-body p-4 text-center">
                        <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                        </div>
                        </div>
                  </div>
                  <div className="col-lg-4 col-xl-4 col-sm-12">
                        <div class="card">
                        <div class="card-header text-center text-uppercase">
                           <h5>home finder</h5>
                        </div>
                        <img src={cardImg2} class="card-img-top" alt="..."/>
                        <div class="card-body p-4 text-center">
                        <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                        </div>
                        </div>
                  </div>
                  <div className="col-lg-4 col-xl-4 col-sm-12">
                        <div class="card">
                        <div class="card-header text-center text-uppercase">
                           <h5>morgage finder</h5>
                        </div>
                        <img src={cardImg3} class="card-img-top" alt="..."/>
                        <div class="card-body p-4 text-center">
                        <p class="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                        </div>
                        </div>
                  </div>
                 </div>
             </div>

            <Footer/>
            
        </React.Fragment>
    );
}


export default Services;


