import React from 'react';


import { FaFacebookF,FaTwitter,FaPinterestP,FaLinkedinIn,FaGooglePlusG,FaInstagram } from 'react-icons/fa';


const Footer = () =>{
    return(
        <>
           <footer className="footer"> 
             <div className="container p-5">
            <div className="row">
               <div className="col-md-12 d-flex justify-content-center">
               <div className="circle">
                 <FaFacebookF color='white' size='1rem'/>
               </div>
               <div className="circle">
                  <FaTwitter color='white' size='1rem'/>
               </div>
               <div className="circle">
                  <FaPinterestP color='white' size='1rem'/>
               </div>
               <div className="circle">
                  <FaLinkedinIn color='white' size='1rem'/>
               </div>
               <div className="circle">
                  <FaGooglePlusG color='white' size='1rem'/>
               </div>
               <div className="circle">
                  <FaInstagram color='white' size='1rem'/>
               </div>
             </div>

             <div className="col-12 py-3 d-flex justify-content-center">
               <p>Keas 69 Str.15234, Chalandri Athens, Greece</p>
               <p>copy right 2021 realkey all rights reserved</p>
               <div className="fmenu">
               <ul>
                   <li><a href="">Privicy Policy</a></li>
                   <li><a href="">Terms of use</a></li>
               </ul>
               </div>
            </div>
            </div>
            </div>    
        </footer>
         
        </>
    );
}


export default Footer;



